import React, { Component, Fragment } from 'react';
import { Col, Collapse } from 'reactstrap';


export default class CollabCard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false
        };
    }

    onMouseOut = function () {
        this.setState({ isOpen: false });
    }.bind(this)

    onMouseOver = function () {
        this.setState({ isOpen: true })
    }.bind(this)

    render() {
        return (
            <Fragment>
                <Col sm={4} onMouseOver={this.onMouseOver} onMouseOut={this.onMouseOut}>
                    <div className="collabcard">
                        <img src={this.props.img} className={(this.props.disabled ? "disabled" : "")} />
                        <Collapse isOpen={this.state.isOpen}>
                            <div className={`text ${this.props.urls?"pb-0":""}`}>
                                {this.props.text}
                            </div>
                            {
                                this.props.urls
                                    ? <div className="urls">
                                        {this.props.urls.map((item, key) => {
                                            return <a href={item.url}>{item.text}</a>;
                                        })}
                                    </div>
                                    : null
                            }

                            {
                                (this.props.tags)
                                ?(
                                    <div className="tags">
                                        <div>Tags:</div>
                                        {this.props.tags.map((item, key) => {
                                            return <span key={key} className={`badge badge-${item.color}`}>{item.text}</span>;
                                        })}
                                    </div>
                                )
                                :null
                            }
                            {
                                (this.props.lic)
                                ?<div className="license">
                                    {this.props.lic}
                                </div>
                                :null
                            }
                        </Collapse>
                    </div>
                </Col>
            </Fragment>
        );

    }
}