import React, { Component, Fragment } from 'react';
import Icon from '@mdi/react';
import { mdiGithub, mdiNpm, mdiHandHeart } from '@mdi/js';
import { Card, CardBody, Row, Col, CardTitle, Collapse } from 'reactstrap';
import * as urls from './../config.json';
import { local_urls } from './../layout/default/urls';
import { CollabCard } from './components';
import collab from './data/collab.json';
import projects from './data/projects.json';
import others from './data/other-references.json';

export default class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    render() {
        return (
            <Fragment>

                <section className="projects" id="projects" >
                    <h2>
                        Projects
                    </h2>
                    <Row>
                        {
                            projects.map((item, key) => {
                                return <CollabCard key={key} {...item} />
                            })
                        }
                    </Row>
                </section>

                <section className="collab" id="collaboration" >
                    <h2>
                        Long-term collaboration
                    </h2>
                    <Row>
                        {
                            collab.map((item, key) => {
                                return <CollabCard key={key} {...item} />
                            })
                        }
                    </Row>
                </section>

                <section className="collab" id="other-references" >
                    <h2>
                        Other references
                    </h2>
                    <Row>
                        {
                            others.map((item, key) => {
                                return <CollabCard key={key} {...item} />
                            })
                        }
                    </Row>
                </section>
            </Fragment>
        );

    }
}